import {useState, useEffect} from 'react';
import {useLocation, useHistory} from 'react-router-dom';

import {getAllCategories} from '../../api';


import Preloader from '../Preloader';
import Categories from '../CategoryList';
import Search from '../Search';

function Home() {
    const [categories, setCategories] = useState([]);
    const [filteredCategories, setFilteredCategories] = useState([]);

    const {pathname, search} = useLocation();
    const {push} = useHistory();

    const searchHandler = (inputValue) => {
        setFilteredCategories(
            categories.filter(
                item => item.strCategory.toLowerCase().includes(inputValue.toLowerCase())
            )
        );
        push({pathname, search: `?search=${inputValue}`});
    }

    useEffect(() => {
        getAllCategories().then(data => {setCategories(data.categories)});
        getAllCategories().then(data => {setFilteredCategories(
            search? 
            data.categories.filter(item => 
                item.strCategory.toLowerCase().includes(
                    search.split('=')[1]
                )) : 
                data.categories
        )});
    }, [search]);

    return (
        <>
            <Search searching={searchHandler}/>
            {!categories.length ?
            <Preloader /> : 
            <Categories categories={filteredCategories}/>}
        </>
    )
}

export default Home;
